import React, { useEffect, useState } from 'react';
import axios from '../axiosConfig';

export default function WishList({ wishlist, addToCart, removeFromWishlist }) {
    const [products, setProducts] = useState({}); 

    useEffect(() => {
        if (wishlist.length === 0) {
            console.log("Wishlist is empty, skipping fetch.");
            return;
        }

        const fetchProductDetails = async () => {
            const productDetails = {};
            const validWishlist = wishlist.filter(item => {
                const id = typeof item === 'string' ? item : item?.productId?._id;
                if (!id) console.warn("Skipping invalid wishlist item without a valid productId:", item);
                return id !== undefined && id !== null;
            });

            for (const item of validWishlist) {
                const id = typeof item === 'string' ? item : item.productId._id;

                if (!products[id]) {
                    try {
                        console.log(`Fetching details for productId: ${id}`);
                        const response = await axios.get(`http://16.171.17.140:5000/api/products/${id}`);
                        if (response.data) {
                            productDetails[id] = response.data;
                        }
                    } catch (error) {
                        console.error(`Error fetching product ${id}:`, error);
                    }
                }
            }

            setProducts(prevProducts => {
                const cleanProducts = Object.fromEntries(
                    Object.entries(prevProducts).filter(([key, value]) => value !== undefined)
                );
                return { ...cleanProducts, ...productDetails };
            });
        };

        fetchProductDetails();
    }, [wishlist]);

    return (
        <div>
            <h2>Your Wishlist</h2>
            {wishlist.length === 0 ? (
                <p>Your wishlist is empty</p>
            ) : (
                <div>
                    {console.log("State Products", products)}
                    {wishlist.map((productId) => {
                        const id = typeof productId === 'string' ? productId : productId.productId._id;
                        const product = products[id];
                        console.log("Product Detail: ", product);

                        
                        if (!product) {
                            return <p key={id}>Loading product details...</p>;
                        }

                        return (
                            <div key={product._id} className="wishlist-item">
                                <img src={product.image} alt={product.name} />
                                <h3>{product.name}</h3>
                                <p>Price: ${product.price}</p>
                                <button onClick={() => addToCart(product)}>Move to Cart</button>
                                <button onClick={() => removeFromWishlist(product._id)}>Remove from Wishlist</button>
                            </div>
                        );
                    })}
                </div>
            )}
        </div>
    );
} 