import React, { createContext, useState, useEffect } from 'react';
import axios from 'axios';

export const UserContext = createContext();
export const UserProvider = ({ children }) => {
    const [user, setUser] = useState(null);
    const [token, setToken] = useState(null);
    useEffect(() => {
        const savedToken = localStorage.getItem('token');
        if (savedToken) {
            setToken(savedToken);
        }
    }, []);
    const login = async (email, password) => {
        try {
            const response = await axios.post('http://18.117.182.184:5000/auth/login', {
                email, password
            });
            const { token } = response.data;
            setToken(token);
            localStorage.setItem('token', token);
            return {success: true};
        } catch (err) {
            console.error('Error logging in:', err);
            return {success: false};
        }
    };
    const register = async (username, email, password) => {
        try {
            const response = await axios.post('http://18.117.182.184:5000/auth/register', {
                username, email, password
            });
            return {success: true};
        } catch (err) {
            console.error('Error registering:', err);
            return {success: false};
        }
    };
    const logout = () => {
        localStorage.removeItem('token');
        setToken(null);
        setUser(null);
    };

    return (
        <UserContext.Provider value={{ user, setUser, token, login, register, logout }}>
            {children}
        </UserContext.Provider>
    )
}