import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import { UserContext, UserProvider } from "./UserContext";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import ProductList from "./components/ProductList";
import ProductDetails from "./components/ProductDetails";
import Cart from "./components/Cart";
import WishList from "./components/WishList";
import NavBar from "./components/NavBar";
import Register from "./components/Register";
import Login from "./components/Login";
import "./App.css";
import UserProfile from "./components/UserProfile";

function App() {
    const [cart, setCart] = useState([]);
    const [wishlist, setWishlist] = useState([]);
    const [products, setProducts] = useState([]);
    const [searchTerm, setSearchTerm] = useState("");
    const [filteredProducts, setFilteredProducts] = useState([]);
    const { token } = useContext(UserContext); // Access token from UserContext


    useEffect(() => {
        const fetchProducts = async () => {
        try {
            const response = await axios.get("/api/products");
            setProducts(response.data);
            setFilteredProducts(response.data);
        } catch (error) {
            console.error("Error fetching products", error);
        }
        };

        const fetchCartAndWishlist = async () => {
        if (!token) {
            setCart([]);
            setWishlist([]);
            return;
        }
        try {
            const cartResponse = await axios.get("/api/cart");
            setCart(cartResponse.data.items || []);

            const wishlistResponse = await axios.get("/api/wishlist");
            setWishlist(wishlistResponse.data.items || []);
        } catch (error) {
            console.error("Error fetching cart or wishlist:", error);
        }
        };

        fetchProducts();
        fetchCartAndWishlist();
    }, [token]);

    const addToCart = (product) => {
        if (!token) {
        alert("Please log in to add items to your cart.");
        return;
        }
        axios
        .post("/api/cart", {
            productId: product._id,
            quantity: 1,
        })
        .then((response) => {
            setCart(response.data.items);
        })
        .catch((error) => {
            console.error("Error adding to cart:", error);
        });
    };

    const addToWishlist = (product) => {
        if (!token) {
        alert("Please log in to add items to your wishlist.");
        return;
        }
        axios
        .post("/api/wishlist", {
            productId: product._id,
        })
        .then((response) => {
            setWishlist(
            response.data.items.map((item) => item.productId || item._id)
            );
        })
        .catch((error) => {
            console.error("Error adding to wishlist:", error);
        });
    };

    const removeFromCart = (productId) => {
        if (!token) {
        alert("Please log in to remove items from your cart.");
        return;
        }
        axios
        .delete(`/api/cart/${productId}`)
        .then((response) => {
            setCart(response.data.items);
        })
        .catch((error) => {
            console.error("Error removing from cart:", error);
        });
    };

    const removeFromWishlist = async (productId) => {
        if (!token) {
        alert("Please log in to remove items from your wishlist.");
        return;
        }
        try {
        await axios.delete(`/api/wishlist/${productId}`);
        setWishlist((prevWishlist) =>
            prevWishlist.filter((item) => {
            const id = typeof item === "string" ? item : item.productId._id;
            return id !== productId;
            })
        );
        } catch (error) {
        console.error("Error removing from wishlist:", error);
        }
    };

    const incrementQuantity = (productId) => {
        if (!token) {
        alert("Please log in to update your cart.");
        return;
        }
        axios
        .post("/api/cart", {
            productId: productId,
            quantity: 1,
        })
        .then((response) => {
            setCart(response.data.items);
        })
        .catch((error) => {
            console.error("Error updating cart quantity:", error);
        });
    };

    const decrementQuantity = (productId) => {
        if (!token) {
        alert("Please log in to update your cart.");
        return;
        }
        const currentItem = cart.find((item) => item.productId === productId);
        if (currentItem && currentItem.quantity > 1) {
        axios
            .post("/api/cart", {
            productId: productId,
            quantity: -1,
            })
            .then((response) => {
            setCart(response.data.items);
            })
            .catch((error) => {
            console.error("Error updating cart quantity:", error);
            });
        }
    };

    const moveToWishlist = (product) => {
        if (!token) {
        alert("Please log in to move items to your wishlist.");
        return;
        }
        // First, remove from cart
        removeFromCart(product._id);
        // Then, add to wishlist
        addToWishlist(product);
    };

    const handleSearch = (term) => {
        setSearchTerm(term);
        if (term) {
        const filtered = products.filter(
            (product) =>
            product.name.toLowerCase().includes(term.toLowerCase()) ||
            product.description.toLowerCase().includes(term.toLowerCase())
        );
        setFilteredProducts(filtered);
        } else {
        setFilteredProducts(products);
        }
    };

    return (
        <Router>
        <NavBar searchTerm={searchTerm} handleSearch={handleSearch} />
        <Routes>
            <Route
            path="/"
            element={
                <ProductList
                products={filteredProducts}
                addToCart={addToCart}
                addToWishlist={addToWishlist}
                />
            }
            />
            <Route
            path="/product/:id"
            element={
                <ProductDetails
                products={products}
                addToCart={addToCart}
                addToWishlist={addToWishlist}
                />
            }
            />
            <Route
            path="/cart"
            element={
                <Cart
                cart={cart}
                removeFromCart={removeFromCart}
                incrementQuantity={incrementQuantity}
                decrementQuantity={decrementQuantity}
                moveToWishlist={moveToWishlist}
                />
            }
            />
            <Route
            path="/wishlist"
            element={
                <WishList
                wishlist={wishlist}
                addToCart={addToCart}
                removeFromWishlist={removeFromWishlist}
                />
            }
            />
            <Route
            path="/profile"
            element={<UserProfile/>}
            />
            <Route
            path="/register"
            element={<Register/>}
            />
            <Route
            path="/login"
            element={<Login/>}
            />
        </Routes>
        </Router>
    );
    }

export default function WrappedApp() {
    return (
        <UserProvider>
            <App />
        </UserProvider>
    );
}
